import taskStack from './taskStack'
import {getQueryString,type} from '../util/util'
import history from './history'
import supreme from "../supreme";
export default {
    // 
    add(params){
        var task = taskStack.add(params);
        this.asyncTaskStack(taskStack.getStack());
        return task;
    },
    addTask(task){
        var task = taskStack.addTask(task);
        this.asyncTaskStack(taskStack.getStack());
        return task;
    },
    //
    pop(){
        this.asyncTaskStack();
        var task =taskStack.pop();
        return task;
    },
    //vue task
    fillVueComponentToTask(gid,vue){
        // task taskStack 
        var task = taskStack.fillVueComponentToTask(vue,supreme.getQueryObject());
        // 
        if(taskStack.getStack().length==1){
            this.asyncTaskStack()
        }
        return task;
    },
    // 
    isWebBowserBack(){
        if(supreme.getQueryString("")&&supreme.getQueryString("")){

        }
    },
    // task
    backToTaskByName(name){
        return taskStack.backToTaskByName(name);
    },
    // task 
    backToTaskByStep(step){
        return taskStack.backToTaskByStep(step);
    },
    // vue 
    triggerVueComBack() {
        var task = this.getCurrentTask();
        task.back();
    },
    // vue appear
    triggerVueComAppear(info){
        var task = this.getCurrentTask();
        if(task){
          if(type.isFunction(task.appear)){
            task.appear(info);
          }
        }else{
          //todo
        }
    },
      // vue  networkChange
    triggerVueNetworkChange(status){
        var task = this.getCurrentTask();
        if(task){
        if(type.isFunction(task.networkChange)){
            task.networkChange(status)
        }
        }else{
        // todo
        }

    },
    // 
    getTaskStack(){
        return taskStack.getStack();
    },
    //
    isEmpty(){
        return taskStack.isEmpty();
    },
    getCurrentTask(){
        return this.getLastTask();
    },
    getLastTask(){
        return taskStack.getLastTask();
    },
    //task
    getTaskStack(){
      return taskStack.getStack();
    },

    clear(){
        taskStack.clear();
    },
    asyncTaskStack(){

        var currentTaskArray = taskStack.getStack();
        var hisTaskArray = history.getLocalTaskHistory();

        if(currentTaskArray.length==1&&hisTaskArray.length>=2){
            this.restoreHistoryToTaskStack();
        }
        history.asyncTaskStack(taskStack.getStack())

    },
    // task 
    restoreHistoryToTaskStack(){
        // task
        var task = this.getLastTask();
        var historyTaskStack = history.getLocalTaskHistory();
        var index=1;
        for(var i =0;i<historyTaskStack.length;i++){
            if(historyTaskStack[i].pathName.replace("/","")==task.pathName.replace("/","")){
                index=i;
                break;
            }
        }
        var resultTaskStack = historyTaskStack.slice(0,index+1);
        taskStack.setStack(resultTaskStack);
    },
    // 
    getDirection(){
        //direction : back forward
        var gid = getQueryString("url_gid");
        var tkStack=taskStack.taskStack;
        var abandonTaskStack=taskStack.abandonTask;
        var direction="forward";
        for(var i=0;i<tkStack.length;i++){
            var task = tkStack[i];
            if(task.gid==gid){
                direction="back";
            }
        }

        return direction;
    },

}



