// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import FastClick from 'fastclick';
import App from './App'
import router from './router'
import store from './store/store'
import './assets/scss/myMint.css'
import './util/rem'
import { supreme, taskMgr } from '@/supreme-core'
import baseConfig from '@/config/baseConfig'
import util from '@/util/util'
import hybrid from '@/util/reactNativeHybrid'
import MtaH5 from 'mta-h5-analysis';

supreme.init({$router: router});
// MTAsid
MtaH5.init({
  "sid":'500659861', //appid
  // "cid":'********', //
  "autoReport":1,//(1:init,0:pgv)
  "senseHash":0, //hashurl
  "senseQuery":0, //urlurl
  "performanceMonitor":0,//
  "ignoreParams":[] //url
});
// 
Vue.filter('precisionTwo', function (value) {
  if (!value) return ''
  value = value.toString()
  return Number((value).match(/^\d+(?:\.\d{0,2})?/))
});
//ios
if ('addEventListener' in document) {
  document.addEventListener('DOMContentLoaded', function() {
      FastClick.attach(document.body);
  }, false);
}
var main={
  // 
  initBaseData(callback){
    if(baseConfig.enableInfiniteScroll){
      var InfiniteScroll =  require("mint-ui").InfiniteScroll;
      Vue.use(InfiniteScroll);
    }
    if(baseConfig.enableImageLazyLoad){
      var Lazyload =  require("mint-ui").Lazyload;
      Vue.use(Lazyload, {
        preLoad: 1.3,
        loading:  require('./assets/svg/img_loading.svg'),
        attempt: 1
      })
    }
    if(baseConfig.enableMock){
      require('@/mock/mock');
    }
    callback();
  },
  initVue(){
    this.initBaseData(()=>{
      new Vue({
        el: '#app',
        router,
        store:store,
        template: '<App/>',
        components: { App }
      })
    });
  },
  init(){
    this.initVue()
    // 
    if(util.newVersion()){
      // supreme.showToast('1.3.4');
      console.log('1.3.6');
      // appear
      dsBridge.register('appear', function (info) {
        taskMgr.triggerVueComAppear(info)
      });
      // 
      dsBridge.register('networkChange', function (info) {
        taskMgr.triggerVueNetworkChange(info);
      });
      // 
      let userInfo = dsBridge.call("common.getUserInfo", "")
      supreme.localCache.set("userInfo", userInfo);
      // 
      var OSInfo = dsBridge.call("common.getOSInfo", "");
      supreme.localCache.set("getOSInfo", OSInfo);
    }else{
      util.isInWebview()&&hybrid.controlBackPress({isControl:true},function(){
        taskMgr.triggerVueComBack()
      });
    }
    
      
    /**
    util.isInWebview()&&hybrid.controlWebviewAppear(function(){
      //self.back();
      taskMgr.triggerVueComAppear.bind(taskMgr);
    })
     */

  }

}
/* eslint-disable no-new */

main.init();



