import {guid ,getQueryString} from '../util/util'
import startModel from './taskStartModel'
import $ from 'webpack-zepto'
/**
 * @class Task
 * @export Task
 * @author lei.you
 * @desc 
 *
 */
class Task{
  /**
   * @constructor
   * @param {object} {
   *  id: ID
   *  gid: ID
   *  pathName: 
   *  params:
   *  pageParams: 
   *  keepAlive
   *  vue: vue
   *  model
   * }
   * */
  constructor(params={}){
    if(params.params){
      this.gid=params.params.url_gid;
    }
    this.pathName;
    this.name;
    this.params;
    this.pageParams;
    this.keepAlive;
    this.vue;
    this.model;
    $.extend(this,params);
    this.id=guid.getGuid();
  }
  /**
   * @desc : task 
   * */
  back(){
    if(this.vue){
      console.log(this.vue);
      this.vue.backToLastPage();
    }else{
      throw ("task vue is undefined......");
    }
  }

 /**
   * @desc : native onResume, activity 
   * @param : data 
   *
   * */
  appear(data){
    if(this.vue){
      this.vue.$options.appear.bind(this.vue)(data)
    }else{
      throw ("current task vue is undefined......");
    }
  }

  /*triggerVueComAppear*
   * @description native h5 
   * @param status    wifi 
   * */
  networkChange(status){
    if(this.vue){
      this.vue.$options.networkChange.bind(this.vue)(status);
    }else{
      throw ("current task vue is undefined......");
    }
  }
  static model=0;

}
export default Task;



