import supreme from './supreme'
import hybridFn from './hybrid/hybridFn'
import BaseModel from './model/BaseModel'
import BaseStore from './store/BaseStore'
import taskMgr from './task/taskMgr'
import {setCookie,getCookie,guid,validators,getQueryString,type,getTimestamp,copy} from './util/util'
import  {baseMixin,
  gestureBackMixin,
  scrollBehaviorMixin,
  singleSwitchMixin,
  scrollBannedClickMixin,
  gestureMixin} from './reuse/mixin'
/**
 *@exports index
 *@desc supreme-core module function  classmodule class
 *@author lei.you
 *@date 2018/3/20
 * */
export {


    /**
     * @property {module }
     * @desc module supreme 
     * */
    supreme,
      /**
       * @property {module }
       * @desc module hybridFn hybrid  bridge native 
       * */
    hybridFn,
      /**
       * @property {class }
       * @desc class model 
       *
       * */
    BaseModel,
      /**
       *  @property {class }
       * @desc   
       * */

    BaseStore,
    /**
     *  @property {module }
     * @desc  task task stack 
     * */
    taskMgr,

    /**
       @property {function}
       @desc setCookie
       */
    setCookie,
    /**
     @property {function}
     @desc setCookie
     */
    getCookie,
    /**
     @property {module}
     @desc guid
     */
    guid,
    /**
     @property {module}
     @desc validators
     */
    validators,
    /**
     @property {function}
     @desc getQueryString
     */
    getQueryString,
    /**
     @property {module}
     @desc type
     */
    type,
    /**
     @property {function}
     @desc getTimestamp
     */
    getTimestamp,
    /**
     @property {module}
     @desc copy
     */
    copy,

    /**
     * @property {module}
     * @desc mixin ,vue index.vue mixins extends 
     * taskStack  @module baseMixin*/
    baseMixin,
    /**
     * @property {module}
     * @desc  @module gestureBackMixin */
    gestureBackMixin,
    /**
     * @property {module}
     * @desc  @module scrollBehaviorMixin */
    scrollBehaviorMixin,
    /**
     * @property {module}
     * @desc singleSwitch @module singleSwitchMixin */
    singleSwitchMixin,
    scrollBannedClickMixin,
    /**
     * @property {module}
     * @desc @module gestureMixin */
    gestureMixin

};



