import { wxCreateTokenModel } from '@/model'
export default{
  WXshare({title='',desc='',link='',imgUrl=''}){
    var ua = navigator.userAgent.toLowerCase();
    if (ua.match(/MicroMessenger/i) != 'micromessenger')return;
    wxCreateTokenModel.execute({ bizData:{appId:"wx90dee60149e63525",url:location.href}}).then((data)=>{
      let {resultDesc ,resultData} = data.data;
      if(resultDesc=='success'){
        wx.config({
          debug: false, // ,apialertpclogpc
          appId: resultData.appId, // 
          timestamp: resultData.timestamp, // 
          nonceStr: resultData.noncestr, // 
          signature: resultData.signature,// 
          jsApiList: [
            'updateAppMessageShareData',
            'updateTimelineShareData'
          ]
        });
        wx.ready(function () {
          // 
          wx.updateAppMessageShareData({ 
            title: title, // 
            desc: desc, // 
            link: link, // JS
            imgUrl: imgUrl, // 
            success: function () {
              // 
            }
          });
    
          wx.updateTimelineShareData({ 
            title: title, // 
            link: link, // JS
            imgUrl: imgUrl, // 
            success: function () {
              // 
            }
          });
        })
        wx.error(function(res){
          console.log(`wx-error:${res}`)
        })
      }
    })
  }
}


